import React from 'react';
import { motion } from 'framer-motion';
import { useLanguage } from '../contexts/LanguageContext';
import ParticleBackground from '../components/ParticleBackground';

const AboutUs = () => {
  const { t, language } = useLanguage();

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
        delayChildren: 0.3,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.8,
        ease: "easeOut",
      },
    },
  };

  return (
    <div className="min-h-screen bg-dark-bg text-white overflow-x-hidden">
      <ParticleBackground />
      
      {/* Header */}
      <motion.header
        initial={{ opacity: 0, y: -50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="relative z-10 py-8 px-4"
      >
        <div className="max-w-6xl mx-auto flex justify-between items-center">
          <motion.a
            href="/"
            whileHover={{ scale: 1.05 }}
            className="text-2xl font-bold gradient-text"
          >
            FEXIO LABS
          </motion.a>
          <motion.button
            whileHover={{ scale: 1.05 }}
            onClick={() => window.history.back()}
            className="px-6 py-2 bg-dark-surface border border-neon-cyan rounded-full text-neon-cyan font-semibold hover:bg-neon-cyan hover:text-dark-bg transition-all duration-300"
          >
            {t('aboutUs.backButton')}
          </motion.button>
        </div>
      </motion.header>

      {/* Main Content */}
      <motion.main
        variants={containerVariants}
        initial="hidden"
        animate="visible"
        className="relative z-10 py-20 px-4"
      >
        <div className="max-w-4xl mx-auto">
          {/* Title */}
          <motion.div variants={itemVariants} className="text-center mb-16">
            <h1 className="text-5xl md:text-7xl font-bold gradient-text mb-6">
              {t('aboutUs.title')}
            </h1>
            <div className="w-32 h-1 bg-gradient-to-r from-neon-blue to-neon-violet mx-auto rounded-full" />
          </motion.div>

          {/* Content */}
          <motion.div variants={itemVariants} className="space-y-12">
            <div className="glass rounded-2xl p-8">
              <h2 className="text-3xl font-bold text-neon-cyan mb-6">
                {t('aboutUs.whoWeAre')}
              </h2>
              <p className="text-gray-300 text-lg leading-relaxed">
                {language === 'tr' 
                  ? "Fexio Labs, mobil uygulama geliştirme, web tasarımı ve yenilikçi teknoloji çözümleri konularında uzmanlaşmış, son teknoloji bir dijital ajansıdır. Mümkün olanın sınırlarını zorlayan olağanüstü dijital deneyimler yaratma konusunda tutkuluyuz."
                  : "Fexio Labs is a cutting-edge digital agency specializing in mobile app development, web design, and innovative technology solutions. We are passionate about creating exceptional digital experiences that push the boundaries of what's possible."
                }
              </p>
            </div>

            <div className="glass rounded-2xl p-8">
              <h2 className="text-3xl font-bold text-neon-violet mb-6">
                {t('aboutUs.ourMission')}
              </h2>
              <p className="text-gray-300 text-lg leading-relaxed">
                {language === 'tr' 
                  ? "Yaratıcılık, teknoloji ve kullanıcı deneyimini birleştiren yenilikçi dijital çözümler aracılığıyla işletmeleri ve bireyleri güçlendirmek. Fikirleri gerçeğe dönüştürme gücüne sahip teknolojiye inanıyoruz."
                  : "To empower businesses and individuals through innovative digital solutions that combine creativity, technology, and user experience. We believe in the power of technology to transform ideas into reality."
                }
              </p>
            </div>

            <div className="glass rounded-2xl p-8">
              <h2 className="text-3xl font-bold text-neon-magenta mb-6">
                {t('aboutUs.whatWeDo')}
              </h2>
              <div className="grid md:grid-cols-2 gap-6">
                <div>
                  <h3 className="text-xl font-semibold text-neon-blue mb-3">
                    {language === 'tr' ? 'Mobil Uygulama Geliştirme' : 'Mobile App Development'}
                  </h3>
                  <p className="text-gray-300">
                    {language === 'tr' 
                      ? 'iOS ve Android platformları için yenilikçi mobil uygulamalar oluşturuyoruz.'
                      : 'Creating innovative mobile applications for iOS and Android platforms.'
                    }
                  </p>
                </div>
                <div>
                  <h3 className="text-xl font-semibold text-neon-cyan mb-3">
                    {language === 'tr' ? 'Web Geliştirme' : 'Web Development'}
                  </h3>
                  <p className="text-gray-300">
                    {language === 'tr' 
                      ? 'Duyarlı ve etkileşimli web siteleri ve web uygulamaları geliştiriyoruz.'
                      : 'Building responsive and interactive websites and web applications.'
                    }
                  </p>
                </div>
                <div>
                  <h3 className="text-xl font-semibold text-neon-violet mb-3">
                    {language === 'tr' ? 'UI/UX Tasarım' : 'UI/UX Design'}
                  </h3>
                  <p className="text-gray-300">
                    {language === 'tr' 
                      ? 'Sezgisel ve güzel kullanıcı arayüzleri ve deneyimleri tasarlıyoruz.'
                      : 'Designing intuitive and beautiful user interfaces and experiences.'
                    }
                  </p>
                </div>
                <div>
                  <h3 className="text-xl font-semibold text-neon-magenta mb-3">
                    {language === 'tr' ? 'Dijital Strateji' : 'Digital Strategy'}
                  </h3>
                  <p className="text-gray-300">
                    {language === 'tr' 
                      ? 'Dijital dönüşüm ve büyüme için stratejik rehberlik sağlıyoruz.'
                      : 'Providing strategic guidance for digital transformation and growth.'
                    }
                  </p>
                </div>
              </div>
            </div>

            <div className="glass rounded-2xl p-8">
              <h2 className="text-3xl font-bold text-neon-blue mb-6">
                {t('aboutUs.contactInfo')}
              </h2>
              <div className="grid md:grid-cols-2 gap-6">
                <div>
                  <h3 className="text-xl font-semibold text-neon-cyan mb-2">{t('aboutUs.website')}</h3>
                  <a href="https://fexiolabs.com" className="text-gray-300 hover:text-neon-blue transition-colors">
                    fexiolabs.com
                  </a>
                </div>
                <div>
                  <h3 className="text-xl font-semibold text-neon-cyan mb-2">{t('aboutUs.email')}</h3>
                  <a href="mailto:support@fexiolabs.com" className="text-gray-300 hover:text-neon-blue transition-colors">
                    support@fexiolabs.com
                  </a>
                </div>
              </div>
            </div>
          </motion.div>
        </div>
      </motion.main>
    </div>
  );
};

export default AboutUs;
